@import './variables';
@import './mixins';

* {
    margin: 0;
    padding: 0;
}

body {
    background: #f8f8f8;
    font-family: 'dejavu sans', sans-serif;
    overflow-x: hidden;
}

nav {
    background: linear-gradient(to right, #123f88, #152b54);
    position: fixed;
}

h2, h3, p {
    color: #152b54;
}

h2 {
    text-transform: uppercase;
}

.footer {
    background: linear-gradient(to right, #123f88, #152b54);
    
    h2 {
        color: #fff;
    }

    ul {
        li {
            color: #fff;
            list-style: none;
        } 
    }
}

.preload{
    position: fixed;
    z-index:99999;
    top:0; left:0;
    width:100%; height:100%;
    opacity:1;
    background-color:#fff;
    background-image: url('./../img/loader.svg');
    background-position:50% 50%;
    background-repeat:no-repeat;    
}


@media only screen and (min-width: 320px) {
    nav {
        z-index: 999;
        @include box(320/3.2+vw, 65/3.2+vw);
    }

    h2 {
        font-size: 18/3.2+vw;
        font-weight: 400;
        padding: 15/3.2+vw 0;
        text-align: center;
    }

    .cards {
        margin: 0 0 0 26.5/3.2+vw;
    }

    .card {
        @include box(265/3.2+vw, auto, #123f88 1/3.2+vw solid, 5/3.2+vw);
        @include margin(0, auto, 20/3.2+vw, auto);

        h3 {
            font-size: 15/3.2+vw;
            padding: 10/3.2+vw;
        }

        p {
            font-size: 13/3.2+vw;
            padding: 0 10/3.2+vw 10/3.2+vw;
        }
    }

    .chart {
        @include box(230/3.2+vw, auto);
        @include margin(0/3.2+vw, 0, 0, 42/3.2+vw);
    }

    .container {
        padding-top: 65/3.2+vw;
    }

    .description {
        font-size: 18/3.2+vw;
        font-weight: 200;
        position: absolute;
        width: 230/3.2+vw;
        margin: 0 44/3.2+vw;
        z-index: 2;
    }

    .footer {
        height: 95/3.2+vw;
        width: 320/3.2+vw;

        h2 {
            float: left;
            margin-left: 10/3.2+vw;
        }

        ul {
            float: right;
            @include margin(15/3.2+vw, 10/3.2+vw, 0, 0);
            li {
                font-size: 12/3.2+vw;
                margin-bottom: 8/3.2+vw;
            } 
        }
    }

    .header .description {        
        bottom: 65/3.2+vw;
    }

    .homem {
        width: 250/3.2+vw;
        margin: 5/3.2+vw 34/3.2+vw;
    }

    .menu {
        margin-top: 20/3.2+vw;
        svg{
            margin: 17/3.2 + vw;
            display: block;
            @include box(30/3.2+vw, 30/3.2+vw);
        }
    }

    .ex {
        #path2752 {
            animation: 300ms oneRotare forwards;
            animation-delay: 300ms;
            transform-origin: 52.8/3.2+vw 75.54/3.2+vw;
        }
        #path2762 {
            animation: 300ms desapear linear forwards;
            transform-origin: 50.25/3.2+vw 77.15/3.2+vw;
        }
        #path2766 {
            animation: 300ms thirdRotare forwards;
            animation-delay: 600ms;
            transform-origin: 54.4/3.2+vw 78.1/3.2+vw;
        }
    }

    @keyframes oneRotare {
        0% { 
            transform: rotate(0);
        }
        100% { 
            transform: rotate(-45deg);
            stroke: #1e2f5e;
        }
    }

    @keyframes desapear {
        0% { 
            opacity: 1;
            transform: rotate(0deg)
        }
        100% { 
            opacity: 0;
            transform: rotate(360deg) 
        }
    }

    @keyframes thirdRotare {
        0% { 
            transform: rotate(0);
        }
        100% { 
            transform: rotate(45deg);
            stroke: #1e2f5e;
        }
    }

    .mulher {
        @include box(265/3.2+vw, 172/3.2+vw);
        @include margin(5/3.2+vw, 0, 0, 27/3.2+vw)
    }

    .second .description {        
        bottom: -50Y/3.2+vw;
    }
Y    .triangle {
        width: 230/3.2 + vw;
        @include margin(-30/3.2+vw, 0, 0, 70/3.2+vw);
    }

    [data-animate] {
        opacity: 0;
        transition: .5s;
    }

    [data-animate="left"] {
        transform: translate3D(0, -20/3.2+vw, 0);
    }

    [data-animate="right"] {
        transform: translate3D(-20/3.2+vw, 0, 0);
    }

    [data-animate="bottom"] {
        transform: translate3D(0, 20/3.2+vw, 0);
    }

    [data-animate].animate {
        opacity: 1;
        transform: translate3D(0, 0, 0);
    }
}

@media only screen and (min-width: 992px) {
    nav {
        z-index: 999;
        @include box(992/9.92+vw, 50/9.92+vw);
    }

    h2 {
        font-size: 40/9.92+vw;
        font-weight: 400;        
    }

    .cards {
        display: inline-flex;
        margin: 0;
    }

    .card {
        @include box(260/9.92+vw, auto, #123f88 1.5/9.92+vw solid, 8/9.92+vw);
        @include margin(0, 15/9.92+vw, 20/9.92+vw, 20/9.92+vw);
        
        h3 {
            font-size: 25/9.92+vw;
            padding: 10/9.92+vw;
        }

        p {
            font-size: 15/9.92+vw;
