    }

    .container {
        padding-top: 60/9.92+vw;
        width: 900/9.92+vw;
        margin: 0 auto;
        clear: both;
    }

    .description {
        font-size: 25/9.92+vw;
        font-weight: 200;        
    }

    .footer {
        height: 175/9.92+vw;
        width: 100%;
        
        h2 {
            text-align: center;
            @include padding(60/9.92+vw, 0, 20/9.92+vw, 20/9.92+vw);
        }

        ul {
            @include margin(70/9.92+vw, 10/9.92+vw, 0, 0);
