    }

    .menu {
        margin-top: 5/9.92+vw;
        svg{
            margin: 10/9.92 + vw;
            @include box(30/9.92+vw, 30/9.92+vw);
            display: block;
        }
    }

    .ex {
        #path2752 {
            animation: 300ms oneRotare forwards;
            animation-delay: 300ms;
            transform-origin: 44.1695936/9.92+vw 61.9496064/9.92+vw;
        }
        #path2762 {
            animation: 300ms desapear linear forwards;
            transform-origin: 41.2927936/9.92+vw 63.3384064/9.92+vw;
        }
        #path2766 {
            animation: 300ms thirdRotare forwards;
            animation-delay: 600ms;
            transform-origin: 43.65/9.92+vw 64.5/9.92+vw;
        }
    }

    .mulher {
        @include box(480/9.92+vw, auto);
        @include margin(0);
        float: right;
    }

    .second {
        margin-top: 20/9.92+vw 0;
        h2 {
            padding: 10/9.92+vw 0;
            text-align: right;
            position: inherit;
            float: right;
        }
        .description {
            bottom: -100/3.2+vw;
            right: -20/3.2+vw;
            position: absolute;
            width: 400/9.92+vw;
        }
    }

    .triangle {
        @include box(400/9.92+vw, auto);
        @include margin(0/9.92+vw, 30/9.92+vw, 0, 0);
        float: right;
    }

    [data-animate] {
        opacity: 0;
        transition: .5s;
    }

    [data-animate="left"] {
        transform: translate3D(-50/9.92+vw, 0, 0);
    }

    [data-animate="right"] {
        transform: translate3D(50/9.92+vw, 0, 0);
    }

    [data-animate="bottom"] {
        transform: translate3D(0, -50/9.92+vw, 0);
    }

    [data-animate].animate {
        opacity: 1;
        transform: translateX(0, 0, 0);
    }
}